.editor {
  background-color: #f2f2f2;
}

.editor__filters {
  display: flex;
  box-sizing: border-box;
  width: 240px;
  margin: 0 auto;
  margin-top: 35px;
  margin-bottom: 35px;
  padding: 0 14px 77px;
}

.editor__filters-button {
  display: block;
  width: 48px;
  height: 48px;
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;

  &--crop {
    margin-right: 32px;
  }

  &--fill {
    margin-right: 33px;
    padding-top: 2px;
  }

  &--contrast {
    padding-top: 2px;
  }
}

.editor__filters-icon {
  opacity: 0.3;

  fill: #283645;

  &--crop {
    opacity: 1;

    fill: #d22856;
  }
}

.editor__filters-button:hover .editor__filters-icon,
.editor__filters-button:focus .editor__filters-icon {
  opacity: 0.5;
}

.editor__filters-button:active .editor__filters-icon {
  opacity: 1;

  fill: #d22856;
}

.editor__filters-item {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    bottom: -75px;
    left: -54px;
    width: 160px;
    height: 1px;
    background-color: #dee0df;
  }
}

.editor__scale {
  display: none;
  width: 240px;
  padding: 0;
  background: transparent;
  cursor: pointer;

  appearance: none;

  &--crop {
    position: absolute;
    bottom: -41px;
    left: -18px;
    display: block;
  }

  &:focus {
    outline: none;

    &::-webkit-slider-thumb {
      background-color: #d22856;
    }

    &::-moz-range-thumb {
      background-color: #d22856;
    }

    &::-ms-thumb {
      background-color: #d22856;
    }
  }

  &:hover::-ms-thumb {
    background-color: #d22856;
  }

  &::-webkit-slider-runnable-track {
    width: 240px;
    height: 2px;
    background: #283645;
    cursor: pointer;
  }

  &::-webkit-slider-thumb {
    width: 14px;
    height: 14px;
    margin-top: -6px;
    background-color: #ffffff;
    border: 2px solid #d22856;
    border-radius: 50%;
    cursor: pointer;

    appearance: none;

    &:hover,
    &:focus,
    &:active {
      background-color: #d22856;
    }
  }

  &::-moz-range-track {
    width: 240px;
    height: 2px;
    background: #283645;
    cursor: pointer;
  }

  &::-moz-range-thumb {
    width: 14px;
    height: 14px;
    margin-top: -6px;
    background-color: #ffffff;
    border: 2px solid #d22856;
    border-radius: 50%;
    cursor: pointer;

    appearance: none;

    &:hover,
    &:active {
      background-color: #d22856;
    }
  }

  &::-ms-track {
    width: 240px;
    height: 2px;
    color: transparent;
    background: transparent;
    border-width: 8px 0;
    border-color: transparent;
    cursor: pointer;
  }

  &::-ms-fill-lower,
  &::-ms-fill-upper {
    background: #283645;
  }

  &::-ms-thumb {
    width: 14px;
    height: 14px;
    background: #ffffff;
    border: 2px solid #d22856;
    border-radius: 50%;
  }
}

.editor__button {
  box-sizing: border-box;
  width: 280px;
  margin-bottom: 14px;
  padding: 12px;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  background-color: #f2f2f2;
  border-radius: 30px;
  outline: none;
  cursor: pointer;

  &--upload {
    color: #d22856;
    border: 3px solid #d22856;

    &:hover,
    &:focus {
      color: #ffffff;
      background-color: #d22856;
    }

    &:active {
      color: rgba(255, 255, 255, 0.3);
    }
  }

  &--cancel {
    color: rgba(40, 54, 69, 0.1);
    border: 3px solid #dedfe1;

    &:hover,
    &:focus {
      color: rgba(40, 54, 69, 0.3);
      border: 3px solid #b5babe;
    }

    &:active {
      color: rgba(255, 255, 255, 0.3);
      background-color: #b5b8bd;
    }
  }
}

@media (min-width: $tablet-width) {
  .editor__form {
    position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: flex-start;
    padding-top: 40px;
    padding-left: 348px;
  }

  .editor__photo {
    position: absolute;
    top: 40px;
    left: 0;
    width: 300px;
    height: 400px;
  }

  .editor__filters {
    display: flex;
    flex-direction: column;
    width: 270px;
    margin: 0;
    margin-top: 7px;
    padding: 0;
    padding-bottom: 30px;
  }

  .editor__filters-item {
    display: flex;
    margin-bottom: 49px;

    &:last-child {
      margin-bottom: 0;
    }

    &::after {
      bottom: -25px;
      left: -4px;
      width: 275px;
    }
  }

  .editor__scale {
    display: block;
    width: 202px;
    margin-top: 5px;

    &--crop {
      position: static;
    }

    &--contrast {
      margin-top: 0;
    }
  }

  .editor__filters-button {
    width: 24px;
    height: 24px;
    margin-right: 25px;
  }

  .editor__filters-icon {
    width: 24px;
    height: 24px;

    &--contrast {
      width: 16px;
      height: 16px;
    }

    &--crop {
      opacity: 0.3;

      fill: #283645;
    }
  }

  .editor__buttons {
    display: flex;
    flex-direction: column;
    margin-top: 62px;
  }

  .editor__button {
    width: 260px;
    margin-bottom: 25px;
  }
}

@media (min-width: $desktop-width) {
  .editor__form {
    padding-left: 390px;
  }

  .editor__filters {
    width: 410px;
    margin-top: -4px;
  }

  .editor__filters-item {
    margin-bottom: 26px;

    &:last-child {
      margin-bottom: 0;
    }

    &::after {
      bottom: -15px;
      left: 2px;
      width: 410px;
    }
  }

  .editor__filters-button {
    width: 46px;
    height: 46px;
    margin-right: 46px;
  }

  .editor__filters-icon {
    width: 48px;
    height: 48px;

    &--contrast {
      width: 32px;
      height: 32px;
    }
  }

  .editor__scale {
    width: 288px;
    margin-top: 4px;
  }

  .editor__buttons {
    flex-direction: row;
    margin-top: 132px;
    margin-left: 2px;
  }

  .editor__button {
    width: 183px;
    margin-right: 43px;
  }
}
